<template>
  <div class="chat-wrapper">
    <div class="chat-header gr" @click.once="chatInit" v-if="!visible">
      <div class="chat-header__text">Шушпанчик на связи!</div>
      <div class="chat-header__photo"></div>
    </div>
    <div class="chat-body gr" v-if="visible">
      <div class="chat-area">
        <div
          class="dialog"
          v-for="(message, index) in lastChat"
          :class="'dialog-'+message.type"
          :key="index"
        >
          <div class="dialog-avatar"></div>
          <div class="dialog-text">{{message.text}}</div>
        </div>
      </div>
      <form class="input-message" @submit.prevent="sendMessage">
        <textarea
          rows="1"
          cols="15"
          v-model.trim="query"
          @submit="sendMessage"
          @keydown="textAreaRowsResize"
          ref="chatInput"
          @keydown.enter.prevent="sendMessage"
          v-focus="focused"
          @focus="focused = true"
          @blur="focused = false"
        />
        <button type="submit">🔥</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { focus } from "vue-focus";

export default {
  name: "Chat",
  directives: { focus: focus },
  data() {
    return {
      info: null,
      query: "",
      visible: false,
      inputMaxRows: 3,
      focused: false,
      lastMessage: "",
      chat: [
        {
          type: "in",
          text: "Шушпанчик ждёт, пока ты ему что-то напишешь :)"
        }
      ]
    };
  },
  computed: {
    lastChat: function() {
      return this.chat.slice(-30); // Лимит на 30 последних сообщений.
    }
  },
  methods: {
    chatInit: function() {
      this.visible = true;
      this.focused = true;
      // document.querySelector('textarea#inputMessageTextarea').focus() //TODO: Добавить автофокус на textarea при открытии чата
    },
    scrollToBotom: function(selector) {
      this.$nextTick(
        () =>
          (document.querySelector(selector).scrollTop = document.querySelector(
            selector
          ).scrollHeight)
      );
    },
    textAreaRowsResize: function() {
      var textarea = this.$refs.chatInput
      console.log(textarea.value);
      var maxrows = this.inputMaxRows;
      var txt = textarea.value;
      var cols = textarea.cols;

      var arraytxt = txt.split("\n");
      var rows = arraytxt.length;

      for (let i = 0; i < arraytxt.length; i++) {
        rows += parseInt(arraytxt[i].length / cols);
      }

      if (rows > maxrows) {
        textarea.rows = maxrows;
      } else {
        textarea.rows = rows;
      }
    },
    sendMessage: function() {
      if (this.lastMessage != this.query && this.query != "") {
        this.lastMessage = this.query;

        this.chat.push({
          type: "out",
          text: this.query
        });

        this.scrollToBotom(".chat-area");

        let data = new FormData();
        data.append("q", this.query);
        axios
          .post("/api/get-answer", data, {
            headers: {
              "Content-type": "application/x-www-form-urlencoded"
            }
          })
          .then(response => {
            if (response.status == 200) {
              this.chat.push({
                type: "in",
                text: response.data.a
              });
            } else {
              this.chat.push({
                type: "in",
                text: "404: Нет соединения с космосом."
              });
            }

            this.scrollToBotom(".chat-area");
          });
        this.query = "";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$shuspanchik-ava: "data:image/jpeg;base64,";
.chat-wrapper {
  position: fixed;
  padding: 10px;
  right: 10px;
  bottom: 10px;
  max-width: 340px;
  z-index: 1000;
  color: #000;
  overflow: hidden;
}

.chat-header {
  $block-height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: $block-height;
  cursor: pointer;
  box-shadow: 0 5px 6px 0px rgba(0, 0, 0, 0.3);
  transition: 0.3s;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 12px 3px rgba(0, 0, 0, 0.3);
  }

  &__text {
    padding: 0 10px 0 25px;
    font-weight: 600;
    color: #3d3d9a;
  }
  &__photo {
    width: $block-height;
    height: $block-height;
    border-radius: $block-height;
    background-image: url($shuspanchik-ava);
    background-size: 100%;
    border: 2px solid #fff;
  }
}

.chat-body {
  margin-top: 15px;
  padding: 15px 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 25px;
  box-shadow: 0 5px 6px 0px rgba(0, 0, 0, 0.3);
  transition: 0.3s;
  max-height: 80vh;
  overflow-y: hidden;
  overflow-x: hidden;
}

.chat-area {
  max-height: 10%;
  width: 100%;
  overflow-y: auto;

  .dialog {
    display: flex;
    flex-direction: row;
    align-items: center;
    max-width: 280px;
    margin: 10px 0;

    .dialog-text {
      margin: 0 10px;
      word-wrap: break-word;
      max-width: 215px;
    }

    .dialog-avatar {
      $block-height: 45px;
      width: $block-height;
      height: $block-height;
      border-radius: $block-height;
      flex-shrink: 0;
      background: #5595c8;
      background-size: 100%;
      border: 2px solid #fff;
    }
  }
  .dialog-out {
    flex-direction: row-reverse;
    .dialog-avatar {
      background-image: url("data:image/jpeg;base64,");
    }
    
  }

  .dialog-in {
    .dialog-avatar {
      background-image: url($shuspanchik-ava);
    }
  }
}

.input-message {
  margin-top: 0px;
  z-index: 100;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: stretch;
  flex-shrink: 0;

  textarea,
  button {
    outline: none;
    font-size: 24px;
  }

  textarea {
    resize: none;
    background: rgba(255, 255, 255, 0.4);
    border: none;
    overflow: auto;
    caret-color: rgb(238, 0, 186);
    border-radius: 15px 0 0 15px;
    width: 100%;
    padding-left: 10px;
  }

  button {
    border-radius: 0 15px 15px 0;
    $border-color: rgb(207, 207, 207);
    border: none;
    border-top: 2px solid transparent;
    border-right: 2px solid transparent;
    border-bottom: 2px solid $border-color;
    min-width: 50px;
    background-color: white;
    transition: .3s;
    cursor: pointer;

    &:hover {
      $border-color: rgba(218, 255, 220, 0.8);
      border: none;
      border-top: 2px solid $border-color;
      border-right: 2px solid $border-color;
      border-bottom: 2px solid $border-color;
    }

    &:active {
      $border-color: rgba(123, 255, 134, 1);
      border: none;
      border-top: 2px solid $border-color;
      border-right: 2px solid $border-color;
      border-bottom: 2px solid $border-color;
    }
  }
}

.gr {
  background: linear-gradient(-45deg, #d6a191, #cf91a9, #d2f3ff, #b6e7dc);
  background-size: 400% 400%;
  animation: Gradient 15s ease infinite;
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
</style>
